
#app {
  width: 100%;
  height: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #fad1d1;
  background-color: #222;
}
.search-fields {
  height: 100%;
  width: 1278px;
  font-size: 5vh;
}
.search-fields input, .search-fields button {
    font-size: 5vh;
}
.test-wrapper {
  position: absolute;
  top: 0;
  left: 0;
  width: 6816px;
  height: 2240px;
  z-index: 666;
  pointer-events: none;
}
.test-wrapper .transparent-image-overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 6816px;
    height: 2240px;
    background-image: image-url("immersion/immersion-test.jpg");
    opacity: 0.25;
}
